import axios from "axios"
import QQMapWX from '@/common/js/qqmap-wx-jssdk.min.js'
var qqmapsdk = new QQMapWX({
    key: 'K5MBZ-4IQLX-UKV4H-ZM4WA-GO5W2-FJFR6' // 
});

export default{
    state:{
		proTitleList:[],
		currentAddress:""
	},
	getters:{//  
		currentAddress:state=>state.currentAddress,
		proTitleList:state=>state.proTitleList
	},
    mutations: {
		ProTitleList(state,proTitleList){
			state.proTitleList = proTitleList;
		},
		CurrentAddress(state,{currentAddress}){
			state.currentAddress = currentAddress;
		}
	},
    actions: {
		getProTitleList({commit},params){
			return new Promise((resolve, reject) => {
				// axios.get('',{
				// 	params:{
				// 		id : params.id,
				// 		page : params.page
				// 	}
				// }).then(res=>{
				// 	commit('list',res.data.items);
				// 	resolve();
				// }).catch(err=>{
				// 	reject();
				// })
				commit('ProTitleList',[{id:0,name:"",pic:"./../123.jpg"},
				{id:1,name:"",pic:"./../123.jpg"},
				{id:2,name:"",pic:"./../123.jpg"},
				{id:3,name:"",pic:"./../123.jpg"},
				{id:4,name:"",pic:"./../123.jpg"},
				{id:5,name:"",pic:"./../123.jpg"},
				{id:6,name:"",pic:"./../123.jpg"},
				{id:7,name:"",pic:"./../123.jpg"},
				{id:8,name:"",pic:"./../123.jpg"},
				{id:9,name:"",pic:"./../123.jpg"},
				{id:10,name:"",pic:"./../123.jpg"},
				{id:11,name:"",pic:"./../123.jpg"},
				{id:12,name:"",pic:"./../123.jpg"},
				{id:13,name:"",pic:"./../123.jpg"},
				{id:14,name:"",pic:"./../123.jpg"}]);
				resolve();
			})
		},
		getCurrentAddress({commit},refresh){
			if(refresh || (this.getters.currentAddress === "")){
				uni.authorize({
				    scope: 'scope.userLocation',
				    success() {
						qqmapsdk.reverseGeocoder({
							success: function(res) {
								commit("CurrentAddress",{currentAddress:res.result.address});
							},
							fail: function(error) {
								uni.showModal({
									title: '',
									content: error,
								});
							}
						})
				    },
					fail() {
						uni.showModal({
							title: '',
							content: "",
						});
					}
				})	
			}
		}
	}
}